import React from "react";
import { Loader } from "view/lib"; 
import { Div, Image } from "view/lib/components";

export const newsSlides = (
  newsArticles: ReadonlyArray<Record<string, string>>
) => !!newsArticles.length
  ? newsArticles.map((article, index) =>
    <React.Fragment key={article.image}>
      <Div style={styles.container}>
        <Div style={{...styles.imageContainer, backgroundImage: `url('${article.thumbnail}')`}}>
          {<Image
            style={styles.image}
            src={article.image} />}        
        </Div>
      </Div>
    </React.Fragment>
  )
  : [<Div style={{...styles.container, ...styles.loading}}>
      <Loader />
    </Div>];

const styles = {
  container: {
    display: "flex",
    flexDirection: "column",
    backgroundColor: "white",
    width: ["40vh", "40vh", "45vh"],
    height: ["40vh", "40vh", "45vh"],
    borderRadius: "2%",
    border: "4px double #ef7e1b",
    marginBottom: ["1%", "1%", "unset"]
  },
  loading: {
    alignItems: "center",
    justifyContent: "center"
  },
  imageContainer: {
    color: "white",
  },
  image: {
    aspectRatio: "1/1",
    width: "100%",
    objectFit: "contain",
    cursor: "pointer"
  },
  placeholderImage: {
    height: ["130px", "130px", "220px"],
    width: ["150px", "150px", "250px"],
    cursor: "pointer"
  },
  text: {
    textAlign: "center",
    fontSize: ["medium", "medium", "x-large"],
    fontStyle: "italic"
  }
} as const;